import React, { useEffect, useRef, useState } from 'react'
import { Transition } from 'react-transition-group'
import { useIsVisible } from 'react-is-visible'

const duration = 1000

const defaultStyle = {
  border: '1px solid white',
  borderRadius: '16px',
  padding: '16px',
  transition: `opacity ${duration}ms ease-in-out, transform ${
    duration * 0.8
  }ms ease-in-out`,
  transform: 'translate(-100%)',
  opacity: 0,
}

const transitionStyles = {
  entering: { opacity: 1, transform: 'translate(0%)' },
  entered: { opacity: 1, transform: 'translate(0%)' },
  exiting: { opacity: 0, transform: 'translate(-100%)' },
  exited: { opacity: 0, transform: 'translate(-100%)' },
}

const Card = (props) => {
  const nodeRef = useRef()
  const isVisible = useIsVisible(nodeRef, { once: true })

  const [inProp, setInProp] = useState(false)

  useEffect(() => {
    setInProp(isVisible)
  }, [isVisible])

  const handleClick = () => {
    setInProp(!inProp)
  }

  return (
    <Transition in={inProp} timeout={duration}>
      {(state) => (
        <div
          onClick={handleClick}
          ref={nodeRef}
          style={{ ...defaultStyle, ...transitionStyles[state] }}
        >
          I'm an animated card!
        </div>
      )}
    </Transition>
  )
}

export default Card
